*{
  margin: 0;
  padding: 0;
}
body,ul,li,a,img,h1,h2,h3,h4,h5,h6,ol{
  list-style: none;
  text-decoration: none;
  outline: none;
}
body{
  font-size: 15px;
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 500;
 
}

.btn{
  margin-right: 10px;
  font-size: 13px;
  margin-bottom: 10px;
}
.form-control{
  font-size: 14px;
  box-shadow: none !important;
}
.alert{
  position: absolute;
  width: 100%;
  top: 56px;
}
span{
  font-weight:700;
  font-size: 18px;
}
.footer{

  width: 100%;
  border-top: #ccc solid 1px;
  padding:10px 0;
 
}